.Reasons {
    padding-top: 80px;
    padding-left: 40px;
    background-color: #363d42;
    
    display: flex;
    grid-template: 2rem;
  }
  
  .left-r {
    flex: 1 1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    grid-auto-rows: 1fr;
  }
  
  .left-r > img {
    object-fit: cover;
  }
  
  .left-r > :nth-child(1) {
    width: 12rem;
    grid-row: 1/3;
    height: 28rem;
  }
  
  .left-r > :nth-child(2) {
    grid-column: 2/4;
    width: auto;
    height: 16rem;
  }
  
  .left-r > :nth-child(3) {
    grid-row: 2;
    grid-column: 2/3;
    width: 14rem;
  }
  
  .left-r > :nth-child(4) {
    grid-row: 2;
    grid-column: 3/4;
    width: 10rem;
    height: 11.2rem;
  }
  
  .right-r {
    flex: 1 1;
    text-transform: uppercase;
    gap: 1rem;
    display: flex;
    flex-direction: column;
  }
  
  .right-r > span {
    font-weight: bold;
    color: var(--deepblue);
  }
  
  .right-r > div {
    color: white;
    font-size: 3rem;
    font-weight: bold;
  }
  
  .details-r {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .details-r > div {
    display: flex;
    font-size: 1rem;
  }